import { Tooltip } from 'antd';
import { formatDistanceToNowStrict, formatRFC7231 } from 'date-fns';
import * as React from 'react';
import { useIntl } from 'react-intl';

export const RelativeTimestamp: React.FunctionComponent<{
  timestamp: Date;
}> = ({ timestamp }) => {
  const { formatMessage } = useIntl();
  const emDash = formatMessage({ id: `em-dash` });
  const [tsRelative, setTsRelative] = React.useState<string>(emDash);

  const updateRelativeTimestamp = React.useCallback(() => {
    setTsRelative(
      formatDistanceToNowStrict(timestamp, {
        addSuffix: true,
      })
    );
  }, [timestamp]);

  React.useEffect(() => {
    updateRelativeTimestamp();

    // Periodically update the relative timestamp:
    const interval = setInterval(updateRelativeTimestamp, 1000);
    return () => clearInterval(interval);
  }, [updateRelativeTimestamp]);

  return <Tooltip title={formatRFC7231(timestamp)}>{tsRelative}</Tooltip>;
};
