import { DeleteOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import { useDispatch } from 'react-redux';
import { surveysDeleteOne } from '../store/surveys';
import { DisplayableSurvey } from './useDisplayableSurveys';

export const SurveyActions: React.FunctionComponent<{
  survey: DisplayableSurvey;
}> = ({ survey }) => {
  const dispatch = useDispatch();

  if (survey.status === `new`) {
    return (
      <Button
        danger
        size={`small`}
        icon={<DeleteOutlined style={{ marginRight: 8 }} />}
        disabled={survey.visibility !== `show`}
        onClick={() => dispatch(surveysDeleteOne({ surveyId: survey._id }))}
      >
        <FormattedMessage id={`list-surveys.action-delete-label`} />
      </Button>
    );
  }

  return null;
};
