import { Checkbox, Typography } from 'antd';
import * as React from 'react';
import { useIntl } from 'react-intl';
import { useSurveyStatusDescriptions } from './surveyStatus';
import { DisplayOptions, StatusFilter } from './useDisplayableSurveys';

export const useStatusFilterConfigurator = (
  options: DisplayOptions,
  updateOptions: (newValue: DisplayOptions) => void
) => {
  const { formatMessage } = useIntl();
  const surveyStatuses = useSurveyStatusDescriptions();
  const hiddenStatusesOptions = Object.entries(surveyStatuses).map(
    ([statusKey, statusDescription]) => ({
      value: statusKey,
      label: statusDescription,
    })
  );

  const statusFilterView = (
    <React.Fragment>
      <Typography style={{ marginBottom: 8, fontWeight: `bold` }}>
        {formatMessage({ id: `list-surveys.status-filter-header` })}
      </Typography>

      <Checkbox.Group
        options={hiddenStatusesOptions}
        defaultValue={options.hiddenStatuses}
        onChange={(newValues) =>
          updateOptions({
            ...options,
            hiddenStatuses: newValues as StatusFilter,
          })
        }
      />
    </React.Fragment>
  );

  return {
    statusFilterView,
  };
};
