import { Button, Divider, PageHeader, Select, Typography } from 'antd';
import * as React from 'react';
import { useIntl } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { LOCALES } from '../i18n/locale';
import { selectAll, selectSettings } from '../store/root';
import { settingsUpdateLocale } from '../store/settings';
import { surveysClearAll } from '../store/surveys';

export const SettingsView: React.FunctionComponent = () => {
  const { formatMessage } = useIntl();
  const history = useHistory();
  const { locale: activeLocale } = useSelector(selectSettings);
  const storeState = useSelector(selectAll);
  const dispatch = useDispatch();

  return (
    <React.Fragment>
      <PageHeader
        style={{ padding: 0, marginBottom: 32 }}
        title={formatMessage({ id: `settings.header` })}
        onBack={() => history.push(`/`)}
      />

      <Typography>{formatMessage({ id: `settings.locale` })}</Typography>
      <Select
        style={{ width: `100%` }}
        onChange={(newLocale) => dispatch(settingsUpdateLocale(newLocale))}
        value={activeLocale}
      >
        {Object.entries(LOCALES).map(([localeKey, localeDescription]) => (
          <Select.Option key={localeKey} value={localeKey}>
            {localeDescription}
          </Select.Option>
        ))}
      </Select>

      <Button
        danger
        style={{ marginTop: 32 }}
        onClick={() => dispatch(surveysClearAll())}
      >
        {formatMessage({ id: `settings.clear-surveys` })}
      </Button>

      <Divider />

      <div
        style={{
          marginTop: 32,
          overflow: `auto`,
          backgroundColor: `lightgrey`,
          padding: 8,
          borderRadius: 8,
          maxHeight: 500,
        }}
      >
        <pre>{JSON.stringify(storeState, null, 2)}</pre>
      </div>
    </React.Fragment>
  );
};
