import { Button, Form, Input, PageHeader } from 'antd';
import * as React from 'react';
import { useIntl } from 'react-intl';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { CustomDatePicker } from '../base/CustomDatePicker';
import { surveysCreateOne } from '../store/surveys';

export const CreateSurveyView: React.FunctionComponent = () => {
  const { formatMessage } = useIntl();
  const history = useHistory();
  const dispatch = useDispatch();
  const [titleValue, setTitleValue] = React.useState<string>();
  const [startDate, setStartDate] = React.useState<Date>();
  const [endDate, setEndDate] = React.useState<Date>();

  const fieldRequiredWarningMsg = formatMessage({
    id: `field-required-warning`,
  });

  return (
    <React.Fragment>
      <PageHeader
        style={{ padding: 0, marginBottom: 32 }}
        title={formatMessage({ id: `create-survey.header` })}
        onBack={() => history.push(`/`)}
      />

      <Form
        onFinish={() => {
          dispatch(
            surveysCreateOne({
              title: titleValue!,
              tsUnlock: startDate!,
              tsExpiry: endDate!,
            })
          );

          history.push(`/list-surveys`);
        }}
      >
        <Form.Item
          label={formatMessage({ id: `create-survey.title-label` })}
          name={`survey-title`}
          rules={[{ required: true, message: fieldRequiredWarningMsg }]}
        >
          <Input
            value={titleValue}
            onChange={(e) => setTitleValue(e.currentTarget.value)}
          />
        </Form.Item>

        <Form.Item
          label={formatMessage({ id: `create-survey.date-range-label` })}
          name={`survey-date-range`}
          rules={[{ required: true, message: fieldRequiredWarningMsg }]}
        >
          <CustomDatePicker.RangePicker
            onCalendarChange={(selectedRange) => {
              const selectedStartDate = selectedRange?.[0];
              const selectedEndDate = selectedRange?.[1];

              if (selectedStartDate) {
                setStartDate(selectedStartDate);
              }

              if (selectedEndDate) {
                setEndDate(selectedEndDate);
              }
            }}
          />
        </Form.Item>

        <Form.Item>
          <Button type={`primary`} htmlType={`submit`}>
            {formatMessage({ id: `create-survey.submit` })}
          </Button>
        </Form.Item>
      </Form>
    </React.Fragment>
  );
};
