import { CheckCircleOutlined, SyncOutlined } from '@ant-design/icons';
import { Table, Tag } from 'antd';
import { ColumnsType } from 'antd/lib/table';
import { TagProps } from 'antd/lib/tag';
import * as React from 'react';
import { useIntl } from 'react-intl';
import { RelativeTimestamp } from '../base/RelativeTimestamp';
import { SurveyActions } from './SurveyActions';
import { SURVEY_STATUS_KEY, useSurveyStatusDescriptions } from './surveyStatus';
import { DisplayableSurvey, SurveyVisibility } from './useDisplayableSurveys';

const visibilityWrapper = (
  visibility: SurveyVisibility,
  content: React.ReactNode
) => {
  if (visibility === `hide`) {
    return {
      props: {
        rowSpan: 0,
        colSpan: 0,
      },
    };
  }

  if (visibility === `deemphasize`) {
    return <div style={{ opacity: 0.2 }}>{content}</div>;
  }

  return content;
};

const StatusCell: React.FunctionComponent<{
  surveyStatus: SURVEY_STATUS_KEY;
}> = ({ surveyStatus }) => {
  const statusDescriptions = useSurveyStatusDescriptions();

  let tagColor: TagProps['color'] = `warning`;
  let tagIcon: React.ReactNode;
  const tagMsg = statusDescriptions[surveyStatus];

  switch (surveyStatus) {
    case `in-progress`:
      tagColor = `processing`;
      tagIcon = <SyncOutlined spin />;
      break;
    case `completed`:
      tagColor = `success`;
      tagIcon = <CheckCircleOutlined />;
      break;
  }

  return (
    <Tag color={tagColor} icon={tagIcon}>
      {tagMsg}
    </Tag>
  );
};

export const SurveysTable: React.FunctionComponent<{
  surveys: Array<DisplayableSurvey>;
}> = ({ surveys }) => {
  const { formatMessage } = useIntl();

  const columns: ColumnsType<DisplayableSurvey> = [
    {
      title: formatMessage({ id: `list-surveys.title-header` }),
      dataIndex: 'title',
      render: (title, record) => visibilityWrapper(record.visibility, title),
    },
    {
      title: formatMessage({ id: `list-surveys.creation-date-header` }),
      dataIndex: 'tsCreated',
      render: (tsCreated, record) =>
        visibilityWrapper(
          record.visibility,
          <RelativeTimestamp timestamp={tsCreated} />
        ),
    },
    {
      title: formatMessage({ id: `list-surveys.status-header` }),
      dataIndex: 'status',
      render: (surveyStatus, record) =>
        visibilityWrapper(
          record.visibility,
          <StatusCell surveyStatus={surveyStatus} />
        ),
    },
    {
      title: formatMessage({ id: `list-surveys.unlock-date-header` }),
      dataIndex: 'tsUnlock',
      render: (tsUnlock, record) =>
        visibilityWrapper(
          record.visibility,
          <RelativeTimestamp timestamp={tsUnlock} />
        ),
    },
    {
      title: formatMessage({ id: `list-surveys.expiry-date-header` }),
      dataIndex: 'tsExpiry',
      render: (tsExpiry, record) =>
        visibilityWrapper(
          record.visibility,
          <RelativeTimestamp timestamp={tsExpiry} />
        ),
    },
    {
      title: formatMessage({ id: `list-surveys.actions-header` }),
      render: (_, record) =>
        visibilityWrapper(record.visibility, <SurveyActions survey={record} />),
    },
  ];

  return (
    <Table
      rowKey={`_id`}
      columns={columns}
      dataSource={surveys}
      scroll={{ x: true }}
      size={`small`}
      bordered={true}
    />
  );
};
